#confirm-delete {
	height: 330px;
	padding: 20px;
}

#confirm-delete.valued {
	height: 363px;
}

.landscape #confirm-delete {
	height: 302px;
}

#delete {
	position:relative;
	top: 40px;
}

#confirm-delete h2 {
	margin-top: 40px;
	margin-bottom: 50px;
}

#confirm-delete button {
	font-size: 26px;
	margin: 0 20px 0 20px;
	padding: 10px 15px 10px 15px;
	-webkit-box-shadow: -1px -2px 2px black inset;
	        box-shadow: -1px -2px 2px black inset;
}

@media (min-width:1200px) {
	#delete {
		top: 0px;
	}

	#confirm-delete button {
		margin: 10px;
	}
}


